.app {
  font-family: Arial, sans-serif;
  padding: 20px;
}

nav {
  margin-bottom: 20px;
}

nav a {
  margin-right: 10px;
  text-decoration: none;
  color: #007bff;
}

nav a:hover {
  text-decoration: underline;
}

.home, .bed-list {
  max-width: 800px;
  margin: 0 auto;
}

.bed-list ul {
  list-style: none;
  padding: 0;
}

.bed-list li {
  padding: 10px;
  border-bottom: 1px solid #ddd;
}

.bed-manage {
  max-width: 800px;
  margin: 20px auto;
}

.bed-manage ul {
  list-style: none;
  padding: 0;
}

.bed-manage li {
  padding: 10px;
  border-bottom: 1px solid #ddd;
}

.add-crop {
  margin-top: 20px;
}

.add-crop select, .add-crop input {
  margin-right: 10px;
  padding: 5px;
}

.add-crop button {
  padding: 5px 10px;
  background-color: #007bff;
  color: white;
  border: none;
  cursor: pointer;
}

.add-crop button:hover {
  background-color: #0056b3;
}

.crop-manage {
  max-width: 800px;
  margin: 20px auto;
}

.crop-manage ul {
  list-style: none;
  padding: 0;
}

.crop-manage li {
  padding: 10px;
  border-bottom: 1px solid #ddd;
  display: flex;
  justify-content: space-between;
}

.add-history {
  margin: 20px 0;
}

.add-history select, .add-history input {
  margin-right: 10px;
  padding: 5px;
}

.add-history button, .crop-manage button {
  padding: 5px 10px;
  background-color: #007bff;
  color: white;
  border: none;
  cursor: pointer;
}

.add-history button:hover, .crop-manage button:hover {
  background-color: #0056b3;
}

.crop-types {
  max-width: 800px;
  margin: 20px auto;
}

.crop-types ul {
  list-style: none;
  padding: 0;
}

.crop-types li {
  padding: 10px;
  border-bottom: 1px solid #ddd;
  display: flex;
  justify-content: space-between;
}

.add-crop-type {
  margin: 20px 0;
}

.add-crop-type input {
  margin-right: 10px;
  padding: 5px;
}

.add-crop-type button {
  padding: 5px 10px;
  background-color: #007bff;
  color: white;
  border: none;
  cursor: pointer;
}

.add-crop-type button:hover {
  background-color: #0056b3;
}

.todo {
  max-width: 800px;
  margin: 20px auto;
}

.todo ul {
  list-style: none;
  padding: 0;
}

.todo li {
  padding: 10px;
  border-bottom: 1px solid #ddd;
  display: flex;
  align-items: center;
  gap: 10px;
}

.todo input {
  padding: 5px;
}

.todo button {
  padding: 5px 10px;
  background-color: #007bff;
  color: white;
  border: none;
  cursor: pointer;
}

.todo button:hover {
  background-color: #0056b3;
}

/* 기존 스타일 유지, 버튼 스타일 통일 */
.btn {
  padding: 8px 16px;
  background-color: #007bff;
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  font-size: 14px;
  transition: background-color 0.2s;
}

.btn:hover:not(:disabled) {
  background-color: #0056b3;
}

.btn:disabled {
  background-color: #cccccc;
  cursor: not-allowed;
}

/* 반응형 디자인 */
@media (max-width: 600px) {
  .home, .bed-manage, .crop-manage, .crop-types, .todo {
    padding: 10px;
    max-width: 100%;
  }

  .btn {
    padding: 6px 12px;
    font-size: 12px;
  }

  .bed-list ul, .bed-manage ul, .crop-manage ul, .crop-types ul, .todo ul {
    padding: 0;
  }

  .bed-list li, .bed-manage li, .crop-manage li, .crop-types li, .todo li {
    flex-direction: column;
    align-items: flex-start;
    gap: 5px;
  }

  .add-crop input, .add-history input, .add-crop-type input, .todo input {
    width: 100%;
    margin-bottom: 10px;
  }
}

.modal {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
}

.modal-content {
  background: white;
  padding: 20px;
  border-radius: 5px;
  width: 400px;
}

.cycle-row {
  display: flex;
  justify-content: space-between;
  margin-bottom: 10px;
}

.cycle-row input {
  width: 150px;
}

.modal-buttons {
  display: flex;
  justify-content: space-between;
  margin-top: 20px;
}